.App {
  width: 100vw;
  height: 100vh;
  background-color: lightblue;
}

.Fader {
  position: fixed;
  z-index: 10;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 1);
  transition: background 4s;


  &.fading {
    background-color: rgba(255, 255, 255, 1)
  }

  &.revealing {
    transition: background 2s;
    background-color: rgba(255, 255, 255, 0)
  }

  &.revealed {
    display: none;
  }
}

.MainBody {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

}